.julekalender {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 15px;
  max-width: 650px;
  margin: 20px auto;
  background-color: #e0f7fa;
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.door-container {
  position: relative;
  width: 100px;
  height: 100px;
  perspective: 1000px;
  cursor: pointer;
}

.door-content {
  position: absolute;
  width: 90%;
  height: 90%;
  background-color: #d7ffd9;
  color: #084c61;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  text-align: center;
  padding: 10px;
  border-radius: 8px;
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.2);
  z-index: 1;
  overflow: hidden;
}

.door {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #8b4513; /* Wood color */
  background-image: linear-gradient(135deg, #8b4513 40%, #a0522d 40%);
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: bold;
  border: 2px solid #543d25;
  border-radius: 8px;
  transform-origin: left;
  transform-style: preserve-3d;
  transition: transform 0.6s ease-in-out;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  z-index: 2;
}

.door.open {
  transform: rotateY(-120deg);
}

.day-number {
  font-size: 24px;
}

.julekalender {
  /* Snow effect background */
  background-image: url("https://www.transparenttextures.com/patterns/snow.png");
  background-size: cover;
  border: 2px solid #c7c7c7;
  border-radius: 15px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
  padding: 10px;
}

.door {
  background-color: #b5651d;
  background-image: linear-gradient(145deg, #b5651d 50%, #7a4417 50%);
  border: 2px solid #3e2723;
}

.door.open .day-number {
  display: none; /* Hide the day number once opened */
}

.door-content {
  font-size: 14px;
  padding: 8px;
  overflow: hidden;
  text-overflow: ellipsis;
}
